// 事件总线的另一种实现方式
import Vue from "vue";
const app = new Vue({});
/*
事件名：mainScroll
含义：主区域滚动条位置变化后触发
参数：
    滚动的dom元素，如果是undefined,则表示dom元素已经不存在
*/

/* 
事件名：setMainScroll
含义：当要设置主区域滚动条位置时触发
参数：
    滚动条的高度
*/
Vue.prototype.$bus = app;

export default app;